import React from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Tabbar from '@vkontakte/vkui/dist/components/Tabbar/Tabbar';
import TabbarItem from '@vkontakte/vkui/dist/components/TabbarItem/TabbarItem';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Icon28HelpOutline from '@vkontakte/icons/dist/28/help_outline';
import Icon28ArticleOutline from '@vkontakte/icons/dist/28/article_outline';
import {platform, IOS, HeaderButton, Separator} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import persik2 from '../img/persik2.png';

const osname = platform();

const Persik2 = ({go, id, fetchedUser}) => (


<Panel id={id}>
		<PanelHeader
    left={<HeaderButton onClick={go} data-to="home">
    {osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
  </HeaderButton>}
>
    Оценка постов</PanelHeader>
		 <Tabbar>
          <TabbarItem
            onClick={go}
            data-to="home"
          ><Icon28ArticleOutline fill="#FFFF00" /></TabbarItem>
          <TabbarItem
            onClick={go}
            data-to="persik"
          ><Icon28HelpOutline /></TabbarItem>
        </Tabbar>
        
        <Group>
          <Div>
          <center><img src={persik2} alt="Persik The Cat" width="100%"/></center>
          <center><i><b><b>• </b> Если Вы нашли интересные посты по игровой тематике</b> и считаете их достойными для большего охвата - смело нажимайте стрелку вверх ⬆.</i><br/></center>
          <h3>Также просим обратить Ваше внимание на следующие критерии:</h3>

— оформление и качество работы;<br/>
— наличие внутренних ссылок в посте (если указан первоисточник, то одобряем, если новостной посредник— игнорируем);<br/>
— информативность (следите, чтобы информация была полезной, структурированной и доступной для большинства пользователей);<br/>
— оригинальность (отслеживайте уникальность публикации, приоритет авторским начинаниям, если в подборках много записей-клонов, это значит, что эксперты работают плохо);<br/>
— актуальность (важно, чтобы пост был релевантным, к примеру посты с распродаж, которые завершились днём ранее одобрять не стоит).<br/><br/>

<b>• </b> Если Вы считаете пост <b>недостойным</b> для большего охвата, если он не придерживается и половины критериев выше - <b>стрелка вниз ⬇.</b><br/>
<b>• </b> Если Вам пост не интересен - лучше <b>пропустите</b> публикацию!<br/>
<Separator style={{ margin: '12px 0' }} />
<h3>👍 Какие посты можно оценивать «положительно» — ⬆:</h3>
<b>• </b> Любой пост, <b>который</b> так или иначе <b>подразумевает в себе тематику Игр — это игровой пост.</b> <i>(Будь он даже смежно связан с тематикой Арт, Юмор, Стиль, IT и т.д.);</i><br/><br/>
<b>• Рекламные посты</b> о скидках, распродажах, бесплатных играх, ведущие на официальные источники <i>(Steam, EGS, PS Store, и т.д);</i><br/><br/>
<b>• </b> Старайтесь помогать развиваться <b>мелким ресурсам.</b> Для этого придётся вылезти из зоны комфорта (ленты рекомендаций) и отправиться в <b>поиск ВК</b> (или Вашу личную ленту при присутствии там таковых). Там находим игровые группы и из них уже пытаемся <b>поднимать</b> посты;<br/><br/>
<b>• Статьи</b>, написанные в самом <b>ВК</b>, можно и нужно поднимать. При условиях, что она: выполнена <b>качественно</b>, является <b>оригинальной</b>, имеет <b>приятное оформление.</b><br/>
<Separator style={{ margin: '12px 0' }} />
<h3>👎 Что не следует выводить в топ — ⬇:</h3>
<b>• </b> Рекламные посты ведущие на <b>сторонние и неофициальные источники</b> <i>(GabeStore, Zaka Zaka и пр.);</i><br/><br/>
<b>• </b> <b>Любые посты с опросниками ВК</b> (не касается глобальных опросников от зарубежных сайтов наподобие Joystick Awards, The Game Awards), по типу “Лучшая игра года!” должны получать заслуженный <b>минус</b>, т.к. это нечестное и бесплатное повышение охвата группе.<br/>
<Separator style={{ margin: '12px 0' }} />
<h3>❗ Спорные ситуации при оценке постов:</h3>
<b>• </b> <b>Не стоит поднимать</b> 18+ контент. В данном случае лучший выход — <b>игнорирование</b> поста;<br/><br/>
<b>• </b> <b>Советуется игнорировать посты от фирм и официальных сообществ</b> дабы не создавать им дополнительную рекламу без нужды <i>(Aorus, VK Gaming и пр.).</i> Если же пост <b>подходит</b> под критерии контента, достойного охвата, то его можно <b>поддержать;</b><br/><br/>
<b>• </b> <b>Конкурсы:</b><br/>
1) <b>Первый вид</b> — конкурсы для привлечения аудитории за счёт репостов, лайков или вступления в группу — <b>даунвоут.</b><br/>
2) <b>Второй вид</b> — это конкурсы, связанные с игровой индустрией (пример: конкурсы от разработчиков и издателей, деятелей игровой индустрии и т.п.) не несущие рекламный характер — оценивается <b>на усмотрение эксперта.</b><br/><br/>
<b>• </b> <b>Киберспорт, коллекционные издания, настольные игры и головоломки</b> — относятся к игровой тематике. Оцениваем как и другие посты;<br/>
<b>• </b> <b>Игровой косплей относится к тематике игр</b>, однако ценность поста определяется мнением эксперта;<br/><br/>
<b>• </b> <b>Мемы и арты с игровой тематикой</b> (обратите внимание, мемы и арты должны быть понятны игровому сообществу);<br/><br/>
<b>• </b> <b>Новости о сериалах и кино</b>, которые не связаны первоисточником с игровыми франшизами, <b>надо перенаправлять в соответствующий хаб</b> (то есть <b>кино</b>). Ближайший пример — <b>факты по сериалу “Ведьмак”</b> отправляются в <b>Кино;</b><br/><br/>
<b>• </b> <b>Новости о железе</b>, за исключением игровых контроллеров, консолей и прочих устройств, непосредственно связанных с геймингом, <b>перенаправляем в IT</b> (рейтинг данным постам <b>не менять, игнорировать</b>);<br/><br/>
<b>• </b> <b>Если публикация не пересекается с тематикой игры</b>, Вам следует изменить тематику поста;<br/><br/>
<b> Если</b> у игровой группы <b>не проставлена тематика</b> — можно собственноручно поставить ее посту, который Вам понравился, чтобы алгоритм увидел его;<br/><br/>
<b>• </b> <b>Если попадается несколько одинаковых постов</b> с одной и той же темой <i>(«анонсирован новый BioShock, разработка идет полным ходом!»)</i>, найдите пару самых информативных и наиболее подходящих для рекомендаций, по Вашему мнению. Остальные <b>проигнорировать, не понижать;</b><br/><br/>
<b>• </b> <b>Игнорировать неактуальные посты со скидками.</b> Обычно люди уже подписаны на несколько сообществ со скидками. Можно оставлять самый лучший пост с бесплатной раздачей;<br/><br/>
<b>• </b> <b>Излишнее тегирование не является поводом минусить пост.</b> Но выглядит неприятно. Если режет глаз — <b>игнорируйте;</b><br/><br/>
<b>• </b> <b>Не ставьте плюсы полупустым постам с ссылкой на сайт</b> <i>(посты от DTF, StopGame, Игромания и пр. авто-репостеров).</i> Поддерживайте только контент размещаемый <b>в самой социальной сети</b> и не написанный ботами;<br/><br/>
<b>• </b> Проверяйте посты на наличие <b>скрытой рекламы;</b><br/><br/>
<b>• </b> <b>Проверяйте</b> прикрепленные к посту <b>фото/картинки</b>, если таковые имеются, на наличие в комментариях к ним ссылок на сторонние ресурсы;<br/><br/>
<b>• </b> <b>Не следуйте за мнением большинства</b>. Вам пост понравился? — <b>апвоут.</b> Даже если там много минусов, главное — чтобы пост <b>соответствовал правилам.</b><br/><br/>
<i><b>Убедительная просьба</b> — не плюсуйте и не поднимайте всякий треш, не относящийся к игровой тематике.</i><br/>
<Separator style={{ margin: '12px 0' }} />
<Button size="xl" onClick={go} data-to="persik3">Далее</Button><br/><br/>
          </Div>
        </Group>
        
	</Panel>
);



Persik2.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
    fetchedUser: PropTypes.shape({
    photo_200: PropTypes.string,
    first_name: PropTypes.string,
    last_name: PropTypes.string,
    city: PropTypes.shape({
      title: PropTypes.string,
    }),
  }),

};

export default Persik2;